.plan-item {
    padding: 1rem;
    display: flex;
    align-items: center; /* 세로 가운데 정렬 */
    cursor: pointer;
    transition: all 0.15s;
    user-select: none;
  }
  
  .plan-item:hover {
    background: #262620;
  }
  
  /* todo-item 사이에 윗 테두리 */
  .plan-item + .plan-item {
    border-top: 1px solid #f1f3f5;
  }
  
  .plan-text {
    flex: 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
    word-break: break-all;
  }
  
  .checked {
    text-decoration: line-through;
    color: #adb5bd;
  }
  
  .check-mark {
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
    color: #3bc9db;
    font-weight: 800;
  }
input.togglecheck{
  size:20px;
  color: #3bc9db;
  border-radius : 4px;
}

.dependency_add{
  margin-left:2.5rem;
}
.dependency_ui{
  display:block;
  background: #252525;
}


  @media (prefers-color-scheme: light) {
    .plan-item:hover{
      background: #FAFAF9;
    }
    .dependency_ui{
      background: rgb(200,200,200);
    }
    .plan-item{

    }
  }