.dep-item {
    margin-left : 5rem;
    padding: 1rem;
    display: flex;
    align-items: center; /* 세로 가운데 정렬 */
    cursor: pointer;
    transition: all 0.15s;
    user-select: none;
    
  }
  
  .dep-item:hover {
    background: #262620;
  }
  
  
  .plan-text {
    flex: 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
    word-break: break-all;
  }
  
  .checked {
    text-decoration: line-through;
    color: #adb5bd;
  }
  
  .check-mark {
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
    color: #3bc9db;
    font-weight: 800;
  }
input.togglecheck{
  size:20px;
  color: #3bc9db;
  border-radius : 4px;
}

  @media (prefers-color-scheme: light) {
    .dep-item:hover{
      background: #FAFAF9;
    }
    .dep-item{
      background: rgb(200,200,200);
    }
  }